import { Trello } from "../types/trello";
import { getCardEstimate, getCurrentWork, getRemainingWorkMinutes, getTotalWorkMinutes } from "../api/estimateApi";
import { formatMinutes } from "../utils/timeParsing";
import { getElapsedMinutes } from "../utils/dateMath";

async function getBadge(t: Trello.PowerUp.IFrame, icon: string): Promise<Trello.PowerUp.CardBadge> {
    const card = await t.card("id", "dueComplete");
    const estimate = await getCardEstimate(t);
    const currentWork = await getCurrentWork(t);

    let totalWork = getTotalWorkMinutes(estimate);
    let remainingWork = getRemainingWorkMinutes(estimate, card.dueComplete, totalWork);

    if (estimate.estimateMinutes === 0 && totalWork === 0)
        throw t.NotHandled();

    if (currentWork?.cardId === card.id) {
        const currentMinutes = getElapsedMinutes(currentWork.startDate);
        totalWork += currentMinutes;
        remainingWork -= currentMinutes;
    }

    const workPart = totalWork > 0
        ? `| W: ${formatMinutes(totalWork)} | R: ${formatMinutes(remainingWork)}`
        : "";

    return {
        text: `E: ${formatMinutes(estimate.estimateMinutes)} ${workPart}`,
        icon: icon,
        color: card.id === currentWork?.cardId ? "red" : undefined,
        refresh: 10, // in seconds
    };
}

export function getCardBadges(t: Trello.PowerUp.IFrame, props: CapabilityProps): Trello.PowerUp.CardBadgeDynamic[] {
    return [{
        dynamic: () => {
            return getBadge(t, props.baseUrl + props.icon.light);
        },
    }];
}
