import { Trello } from "../types/trello";
import { getBoardButton } from "./boardButtonCapability";
import { getCardBackSection } from "./cardBackSectionCapability";
import { getCardBadges } from "./cardBadgesCapability";

const CAPABILITY_PROPS: CapabilityProps = {
    baseUrl: window.location.href.replace(/\/$/, ""),
    icon: {
        dark: "/static/icon-dark.png",
        light: "/static/icon-light.png",
    },
};

export function initializeCapabilities() {
    // console.log("initializing capabilities");

    window.TrelloPowerUp.initialize({
        "board-buttons": (t: Trello.PowerUp.IFrame) => getBoardButton(t, CAPABILITY_PROPS),
        "card-back-section": (t: Trello.PowerUp.IFrame) => getCardBackSection(t, CAPABILITY_PROPS),
        "card-badges": (t: Trello.PowerUp.IFrame) => getCardBadges(t, CAPABILITY_PROPS),
    });
}
